

































import { Component, Vue } from 'vue-property-decorator';
import BankCard from '@/components/recharge/BankCards.vue';
import PaymentChannel from '@/components/recharge/PaymentChannel.vue';
import RechargeAmount from '@/components/recharge/RechargeAmount.vue';
import Remittance from '@/components/recharge/Remittance.vue';
import Tips from '@/components/recharge/Tips.vue';
import NoticePopop from '@/components/common/NoticePopop.vue';

@Component({
  components: {
    BankCard,
    PaymentChannel,
    RechargeAmount,
    Remittance,
    Tips,
    NoticePopop,
  },
})

export default class Home extends Vue {
  private pageTitle?: string = '充值';
  private noticeText?: string = '稳定、快速、笔笔充值100元以商还可赠送限量座骑哦!';
  private showPopup?: boolean = false;
  private selectedBank?: object = {};
  private showNoticePopover() {
    this.showPopup = true;
  }
  private hidePopup(v: boolean) {
    this.showPopup = v;
  }
  selectBankCard(v: any) {
    this.selectedBank = v;
  }
}
