























import { Component, Vue, Prop, Watch } from 'vue-property-decorator';

@Component({})

export default class PaymentChannel extends Vue {
  private activeRecharge?: number = 0;
  private description?: string = '';
  private newBankItem?: object | any = {};
  @Prop({ type: Object, default: {} }) public readonly bank!: object;
  private rechargeList?: any[] | any= [];
  private bankRechargeItem?: object | any = {
    'unionpay': {
      '奖': [
        { name: '银行卡1', description: '', discount: '+3.00%'},
      ],
      '热': [
        { name: 'App支付', description: '支持银行: 融亿建设招商交通中国光大浦发兴业平安华夏', discount: ''},
        { name: 'App支付', description: '1', discount: '+0.30%'},
      ],
    },
    'alipay': {
      '火': {
        '1': [
          { name: '支付宝转卡', description: '', discount: '+3.00%'},
        ]
      },
      '热': {
        '1': [
          { name: 'App支付', description: '赠送5%', discount: ''}
        ],
        '2': [
          { name: '苹果手机专用', description: '支持手机: 安卓x苹果', discount: ''},
          { name: '扫码支付', description: '', discount: ''},
          { name: '转卡支付', description: '', discount: ''},
          { name: 'IOS固码', description: '', discount: ''},
        ],
      },
    },
    'wechat': {
      '火': [
        { name: 'App支付', description: '', discount: ''}
      ],
      '热': [
        { name: '银行卡1', description: '', discount: '+3.00%'},
      ],
    },
  };
  selectRechargeItem(desc: any, key: number) {
    this.description = desc;
    this.activeRecharge = key;
  }
  @Watch('bank')
  onChangeBankItem(value: object, oldValue: object) {
    this.newBankItem = value;
    this.activeRecharge = 0;
    let bankName = this.newBankItem.bankName;
    let bankType = this.newBankItem.type;
    let level = this.newBankItem.level;
    if (level) {
      this.rechargeList = this.bankRechargeItem[bankName][bankType][level];
      this.description = this.bankRechargeItem[bankName][bankType][level][0]['description'];
    } else {
      this.rechargeList = this.bankRechargeItem[bankName][bankType];
      this.description = this.bankRechargeItem[bankName][bankType][0]['description'];
    }
  }
  private mounted() {
  }
}
