










































import { Component, Vue } from 'vue-property-decorator';

@Component({})

export default class RechargeAmount extends Vue {
  private activeAmount?: number = 0;
  private amount?: number | any = 0;
  private isOnSale?: boolean = true;
  private showInput?: boolean = true;
  private isGift?: boolean = true;
  private amountList?: any[] = [
    { value: '50.00', text:''},
    { value: '100.00', text:''},
    { value: '500.00', text:'贈送奥迪x7天'},
    { value: '1000.00', text:'贈送奥迪x7天'},
    { value: '1500.00', text:'贈送奥迪x7天'},
    { value: '1800.00', text:'贈送奥迪x7天'},
    { value: '2000.00', text:'贈送奥迪x7天'},
    { value: '2500.00', text:'贈送奥迪x7天'},
    { value: '3000.00', text:'贈送奥迪x7天'},
  ];
  private selectAmount(v: any, key: number): any {
    this.activeAmount = key;
    this.amount = v;
  }
  get newAmount(): any {
    if (this.amount == 0 ) { return ''; }
    return this.amount;
  }
  set newAmount(newVal: any) {
    if (newVal.detail > 3000) {
      console.log("exceed");
    } else {
      this.amount = newVal;
    }
  }
}
