



























import { Component, Vue } from 'vue-property-decorator';

@Component({})

export default class BankCards extends Vue {
  private activeBank?: number = -1;
  private bankcardList?: any[] | any = [
    { type: '奖', bankName: 'unionpay', desc:'银行卡转账', discount: '赠送3%', level: '', img: 'recharge06.png' },
    { type: '火', bankName: 'alipay', desc:'支付宝转卡', discount: '赠送3%', level: '1', img: 'recharge05.png' },
    { type: '热', bankName: 'alipay', desc:'小额支付宝', discount: '安全、快速', level: '1', img: 'recharge05.png' },
    { type: '热', bankName: 'alipay', desc:'大额支付宝', discount: '稳定', level: '2', img: 'recharge05.png' },
    { type: '火', bankName: 'wechat', desc:'小额微信', discount: '安全、快速', level: '', img: 'recharge04.png' },
    { type: '热', bankName: 'wechat', desc:'小额微信', discount: '快速', level: '', img: 'recharge04.png' },
    { type: '热', bankName: 'unionpay', desc:'手机快捷', discount: '方便', level: '', img: 'recharge06.png' },
  ];
  private getGameImage(img: string) {
    if (img) {
      return require('@/assets/img/recharge/'+img);
    }
    return '';
  }
  selectBankCard(val: any, key: number) {
    this.activeBank = key;
    this.$emit('select-bank-card', val)
  }
  private mounted() {
    this.selectBankCard(this.bankcardList[0], 0);
  }
}
